'use client';

import { useAuth } from '@/hooks/useAuth';

export default function PelangganDashboard() {
    const { user, logout } = useAuth({ middleware: 'auth' });

    return (
        <div className="min-h-screen bg-gray-100 p-8">
            <div className="max-w-4xl mx-auto bg-white p-6 rounded-lg shadow">
                <h1 className="text-2xl font-bold text-blue-600 mb-4">Area Pelanggan</h1>
                <p className="mb-4">Halo, <strong>{user?.name}</strong>!</p>
                <div className="p-4 bg-yellow-50 border border-yellow-200 rounded mb-4">
                    <p>Lihat jadwal lapangan dan riwayat booking Anda di sini.</p>
                </div>
                <button onClick={logout} className="bg-gray-800 text-white px-4 py-2 rounded">Logout</button>
            </div>
        </div>
    );
}